import OrderItem from 'OrderItem'
import 'OrderContent.css'

export default class OrderContent extends React.Component{
  constructor(p){
    super(p)
    let {content, select} = this
    this.state = {
      listVisible: false,
      orderStatus: 'PAID',
    }
  }
  setListVisible = () => {
    this.setState({...this.state, listVisible: !this.state.listVisible})
  }
  setOrderStatus = (e) => {
    let {content, select} = this

    this.setState({...this.state, 
      listVisible: !this.state.listVisible,
      orderStatus: e.target.value.toUpperCase(),
    })

    console.log(e.target.value, this.state.orderStatus)

    if(e.target.value == 'Paid'){
      select.classList.add('paid')
      select.classList.remove('unpaid','shipping','done')
      content.classList.remove('content-done','content-unpaid')
      console.log('p')
    }else if(e.target.value == 'Unpaid'){
      select.classList.add('unpaid')
      select.classList.remove('paid','shipping','done')
      content.classList.add('content-unpaid')
      content.classList.remove('content-done')
      console.log('u')
    }else if(e.target.value == 'Shipping'){
      select.classList.add('shipping')
      select.classList.remove('paid','unpaid','done')
      content.classList.remove('content-done','content-unpaid')
      console.log('s')
    }else if(e.target.value == 'Done'){
      select.classList.add('done')
      select.classList.remove('paid','unpaid','shipping')
      content.classList.add('content-done')
      content.classList.remove('content-unpaid')
      console.log('d')
    }
  }
  render(){
    let {listVisible, orderStatus} = this.state
    return <div className="order-content" ref={ e => this.content = e }>
                <div className="name">
                    <label className='checkbox-wrapper'>
                        <input type="checkbox" value='item'/>
                        <span className='checkmark'></span>
                    </label>
                    <span>Ian Medina</span>
                </div>
                <div className="product-list">
                    <OrderItem></OrderItem>
                    <OrderItem></OrderItem>
                </div>
                <div className="total">
                    <span>$2,200</span>
                </div>
                <div className="cart">
                    <span>2018/6/8</span>
                    <span>13:39</span>
                </div>
                <div className="checkout">
                    <span>2018/6/8</span>
                    <span>20:23</span>
                </div>
                <div className="address">
                    <p>386 Windler Drives Apt. 358</p>
                </div>
                <div className="option">
                   <div className="select" onClick={this.setListVisible} ref = { e => this.select = e }>
                     <span>{orderStatus}</span>
                     <i className="fas fa-caret-down"></i>
                   </div>
                   <div className={`list ${ listVisible && 'visible' }`}>
                     <input type="text" value='Paid' readOnly="readonly" onClick={(e) => this.setOrderStatus(e)} />
                     <input type="text" value='Unpaid' readOnly="readonly" onClick={(e) => this.setOrderStatus(e)} />
                     <input type="text" value='Shipping' readOnly="readonly" onClick={(e) => this.setOrderStatus(e)} />
                     <input type="text" value='Done' readOnly="readonly" onClick={(e) => this.setOrderStatus(e)} />
                   </div>
                </div>
            </div>
  }
}


