import "Header.css"



export default class Header extends React.Component {

    render() {
        return <header>
            <div className="menu">
                <ul>
                    <li>
                        <span className='logo'>Shoptime</span>
                    </li>
                    <NavLink to='/admin' activeClassName='selected'><li>
                        <span>HOME</span>
                    </li></NavLink>
                    <NavLink to='/orders' activeClassName='selected'><li>
                        <span>ORDERS</span>
                    </li></NavLink>
                    <NavLink to='/products' activeClassName='selected'><li>
                        <span>PRODUCT</span>
                    </li></NavLink>
                    <li>
                        <span className='staus'>ADMIN</span>
                    </li>
                </ul>
            </div>
        </header>
    }
}


