import 'ProductPage.css'
import ProductItem from 'ProductItem'


export default class ProductPage extends React.Component{
    constructor(p){
        super(p)
        this.state = {
            newProductVisible: false,
        }
    }
    setNewProductVisible = () => {
        this.setState({...this.state, newProductVisible: !this.state.newProductVisible})
    }
    render(){
        let {newProductVisible} = this.state
        return <div className="product">
            <section className='product-List'>
                <div className="product-status">
                    <div className="selector">
                        <label className='check-wrapper'>
                            <input type="checkbox" />
                            <span className="checkmark"></span>
                        </label>
                        <i className="fas fa-caret-down"></i>
                    </div>
                    <div className="addbtn" onClick={ this.setNewProductVisible }>
                        <h5>ADD NEW PRODUCT</h5>
                        <i className="fas fa-plus"></i>
                    </div>
                </div>
                <div className="product-title">
                    <h5>Product</h5>
                    <h5>Original</h5>
                    <h5>Discount</h5>
                    <h5>Size</h5>
                    <h5>Color</h5>
                    <h5>Inventory</h5>
                    <h5>Status</h5>
                </div>
                <ProductItem key='1'></ProductItem>
                <ProductItem key='2'></ProductItem>
                <ProductItem key='3'></ProductItem>
            </section>
            <section className={`new-product ${newProductVisible && 'visible'}`}>
                <div className="title">
                    <span>ADD NEW PRODUCT</span>
                    <i className="fas fa-times" onClick={this.setNewProductVisible}></i>
                </div>
                <div className="content">
                    <div className="side-left">
                        <div className="img-updater">
                            <div className="updater-btn">
                                <i className="fas fa-cloud-upload-alt"></i>
                                <span>Drag an image or click here to upload</span>
                            </div>
                            <input type="file"/>
                        </div>
                        <div className="img-wrapper">
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/2.jpg)`}}></div>
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/3.jpg)`}}></div>
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/4.jpg)`}}></div>
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/5.jpg)`}}></div>
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/6.jpg)`}}></div>
                            <div className="img" style={{backgroundImage: `url(${process.env.PUBLIC_PATH}asset/7.jpg)`}}></div>
                        </div>
                    </div>
                    <div className="side-right">
                        <h2>Product Discription</h2>
                        <input type="text" className="product-title"/>
                        <textarea className="product-content"/>
                        <h2>Price</h2>
                        <div className="price-wrapper">
                            <div className="price">
                                <div className="price-title">
                                    <span>Original</span>
                                </div>
                                <input type="text"/>
                            </div>
                            <div className="price">
                                <div className="price-title">
                                    <span>Discount</span>
                                </div>
                                <input type="text"/>
                            </div>
                        </div>
                        <h2>Specification</h2>
                        <div className="size-wrapper">
                            <div className="size-detail">
                                <span>Size</span>
                                <select>
                                    <option value="L">L</option>
                                    <option value="M">M</option>
                                    <option value="S">S</option>
                                </select>
                            </div>
                            
                            <div className="size-detail">
                                <span>Color</span>
                                <input type="text"/>
                            </div>
                            
                            <div className="size-detail">
                                <span>Inventory</span>
                                <input type="text"/>
                            </div>
                            
                        </div>
                        <div className="add-size">
                            <span>ADD NEW SPECIFICATION</span>
                            <i className="fas fa-plus"></i>
                        </div>
                        <div className="btn-wrapper">
                            <h5 className="save">SAVE DRAFT</h5>
                            <div className="publish">
                                <span>PUBLISH</span>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <div className="page-num">
                <i className="fas fa-angle-double-left"></i>
                <span className='selected'>1</span>
                <span>2</span>
                <span>3</span>
                <span>4</span>
                <span>5</span>
                <i className="fas fa-angle-double-right"></i>
            </div>
        </div>
    }
}


