import 'ProductItem.css'

export default class ProductItem extends React.Component{
	constructor(p){
		super(p)
		let {selectbtn, item} = this
		this.state = {
			listVisible: false,
			publishValue: 'PUBLISHED',
		}
	}
	setListVisible = () => {
		this.setState({...this.state, listVisible: !this.state.listVisible})
	}
	onPublishChange = (e) => {
		let {selectbtn, item} = this
		this.setState({...this.state, listVisible: !this.state.listVisible, publishValue: e.target.value.toUpperCase()})
		if(e.target.value == 'Published'){
			selectbtn.classList.remove("unpublished")
			item.classList.remove('item-unpublish')
			selectbtn.classList.add("published")
		}else{
			selectbtn.classList.remove("published")
			selectbtn.classList.add("unpublished")
			item.classList.add('item-unpublish')
		}
	}
	render(){
		let {listVisible, publishValue} = this.state
		return <div className="product-item" ref={e => this.item = e}>
			<div className="item-name">
				<label className='check-wrapper'>
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                </label>
				<div className="img" style={{ backgroundImage: `url(${process.env.PUBLIC_PATH}asset/1.jpg)` }}></div>
				<span className='title'>Mauris non tem.</span>
			</div>
			<span className="price">$3,200</span>
			<span className="price">$2,800</span>
			<div className="item-detail">
				<div className="option">
					<span className="size">L</span>
					<div className="color">
						<span>Gray</span>
						<span>Black</span>
					</div>
					<div className="inventory">
						<span>15</span>
						<span>20</span>
					</div>
				</div>
				<div className="option">
					<span className="size">M</span>
					<div className="color">
						<span>Gray</span>
						<span>Black</span>
					</div>
					<div className="inventory">
						<span>15</span>
						<span>20</span>
					</div>
				</div>
				<div className="option">
					<span className="size">S</span>
					<div className="color">
						<span>Gray</span>
						<span>Black</span>
					</div>
					<div className="inventory">
						<span>15</span>
						<span>20</span>
					</div>
				</div>
				
			</div>
			<div className="status">
				<div className="select" ref={ e => this.selectbtn = e }>
					<span onClick={this.setListVisible}>{publishValue}</span>
					<i className="fas fa-caret-down"></i>
					<div className={`list ${ listVisible && 'visible'}`}>
						<input onClick={(e) => this.onPublishChange(e)} value='Unpublished' readOnly="readonly" />
						<input onClick={(e) => this.onPublishChange(e)} value='Published' readOnly="readonly" />
					</div>
				</div>
			</div>
		</div>
	}
}


