import "IndexPage.css"


export default class IndexPage extends React.Component {
	componentDidMount(){
		google.charts.load('current', {'packages':['line']});
	    google.charts.setOnLoadCallback(drawChart);

	    function drawChart() {

	      var data = new google.visualization.DataTable();
	      data.addColumn('number', 'Day(JUN)');
	      data.addColumn('number', '');
	      data.addColumn('number', '');
	      data.addColumn('number', '');

	      data.addRows([
	        [6,  6100, 500, 7200],
	        [7,  5300, 500, 7000],
	        [8,  2000, 2800, 5500],
	        [9,  4100, 3000, 7500],
	        [10,  3700, 2300, 5800],
	        [11,  5200, 800,  6200],
	        [12,  6500, 900,  7700],
	        [13,  4900, 1900, 7200]
	      ]);

	      var options = {
	      	width: 860,
	        height: 300
	      };

	      var chart = new google.charts.Line(document.getElementById('chart'));

	      chart.draw(data, google.charts.Line.convertOptions(options));
	    }
	}
    render() {
        return <div className='index-wrapper'>
        	<section className="index-title">
        		<h2>OVERVIEW</h2>
        		<div className="set-staus">
        			<div className="day">
        				<span>2018/6/6</span>
        				<i className="fas fa-caret-right"></i>
        				<span>2018/6/13</span>
        			</div>
        			<div className="time">
        				<span>Weekly</span>
        				<i className="fas fa-caret-down"></i>
        			</div>
        		</div>
        	</section>
        	<section className="index-top-price">
        		<div className="panel">
        			<div className="title">
        				<i className="fas fa-hand-holding-usd"></i>
        				<h5>TOTAL REVENUE</h5>
        			</div>
        			<div className="num green">54,540</div>
        		</div>
        		<div className="panel">
        			<div className="title">
        				<i className="fas fa-boxes"></i>
        				<h5>TOTAL COST</h5>
        			</div>
        			<div className="num red">12,660</div>
        		</div>
        		<div className="panel">
        			<div className="title">
        				<i className="fas fa-money-bill"></i>
        				<h5>NET INCOME</h5>
        			</div>
        			<div className="num blue">41,880</div>
        		</div>
        	</section>
        	<section className="index-mid-chart panel">
        		<h2>Activity</h2>
        		<div id="chart"></div>
        	</section>
        	<section className="index-bot-info">
        		<div className="panel">
        			<h5>Transaction Website</h5>
        			<div className="item">
        				<i className="fab fa-facebook"></i>
        				<span className="sitename">Facebook.com</span>
        				<span className="view">45,836</span>
        				<div className="num green">
        					<i className="fas fa-arrow-up green"></i>
        					<span>20%</span>
        				</div>
        			</div>
        			<div className="item">
        				<i className="fab fa-google"></i>
        				<span className="sitename">Google.com</span>
        				<span className="view">23,582</span>
        				<div className="num green">
        					<i className="fas fa-arrow-up green"></i>
        					<span>12%</span>
        				</div>
        			</div>
        			<div className="item">
        				<img src={`${process.env.PUBLIC_PATH}asset/shopify.png`}/>
        				<span className="sitename">Shopify.com</span>
        				<span className="view">2,489</span>
        				<div className="num red">
        					<i className="fas fa-arrow-down red"></i>
        					<span>15%</span>
        				</div>
        			</div>
        			<div className="item">
        				<img src={`${process.env.PUBLIC_PATH}asset/wordpress.svg`}/>
        				<span className="sitename">Wordpress.com</span>
        				<span className="view">1,057</span>
        				<div className="num red">
        					<i className="fas fa-arrow-down red"></i>
        					<span>30%</span>
        				</div>
        			</div>
        		</div>
        		<div className="panel">
        			<h5>Latest Orders</h5>
        			<div className="order">
        				<div className="img" style={{ backgroundImage: `url(${process.env.PUBLIC_PATH}asset/1.jpg)` }}></div>
        				<div className="text">
        					<h5>Vintage T-shirt</h5>
        					<span className="time">2018/6/13  13:42</span>
        					<span className="name">Belle Willis</span>
        				</div>
        				<div className="total">
        					<span>Total</span>
        					<span className="num">3,200</span>
        				</div>
        			</div>
        			<div className="order">
        				<div className="img" style={{ backgroundImage: `url(${process.env.PUBLIC_PATH}asset/2.jpg)` }}></div>
        				<div className="text">
        					<h5>Cowboy Jacket</h5>
        					<span className="time">2018/6/13  10:45</span>
        					<span className="name">Adrian Cummings</span>
        				</div>
        				<div className="total">
        					<span>Total</span>
        					<span className="num">2,800</span>
        				</div>
        			</div>
        			<div className="order">
        				<div className="img" style={{ backgroundImage: `url(${process.env.PUBLIC_PATH}asset/3.jpg)` }}></div>
        				<div className="text">
        					<h5>Coach Coat</h5>
        					<span className="time">2018/6/13  8:26</span>
        					<span className="name">Lura Holland</span>
        				</div>
        				<div className="total">
        					<span>Total</span>
        					<span className="num">1,600</span>
        				</div>
        			</div>
        		</div>
        	</section>
        </div>
    }
}


